import React, { useEffect, useState } from 'react';
import { Row, Col } from 'react-bootstrap';
import Img from 'gatsby-image';
import Title from '../components/Title';

const PostDetail = ({ node }) => {
  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);
  const { title, images, description, date, videos, url } = node;

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  return (
    <div className="post-detail-content">
      <div className="post-detail-header">
        {/* <h2 className="post-title">{title}</h2> */}
        <Title title={title} />
        <p className="post-date">{date}</p>
      </div>
      <p className="description">
        {description.description}
        <br></br>
        {url && (
          <a
            target="_blank"
            rel="noopener noreferrer"
            className="cta-btn color-text link-text"
            href={url}
          >
            [Link]
          </a>
        )}
      </p>

      {images &&
        images.map((image) => {
          return <Img className="post-image" fluid={image.fluid} />;
        })}
      {videos != null &&
        videos.map((video) => {
          return (
            <div className="video-wrapper">
              <video className="post-video" src={video.file.url} controls></video>
            </div>
          );
        })}
    </div>
  );
};

export default PostDetail;
